import React, { Component } from 'react'
import FocusTrap from 'focus-trap-react'

class SpecModal extends Component {

  render() {
    const { onSpecModalState, onCloseSpecModal, onKeyPressValidate } = this.props

    return (
      <FocusTrap>
        <div className="modalContainer" aria-modal="true" role="dialog" aria-label="equipment specs">
          <span className="closeModalButton" tabIndex="0" role="button" aria-label="close" onClick={() => {onCloseSpecModal()}}
                onKeyPress={(event) => { if (onKeyPressValidate(event)) {onCloseSpecModal();} }}>&times;</span>
          <img className="modalPicture" src={onSpecModalState.currentSpecP1} alt={"equipment spec page one"} onClick={() => {onCloseSpecModal()}}
               tabIndex="0" onKeyPress={(event) => { if (onKeyPressValidate(event)) {onCloseSpecModal();} }}/>
          <img className="modalPicture" src={onSpecModalState.currentSpecP2} alt={"equipment spec page two"} onClick={() => {onCloseSpecModal()}}
               tabIndex="0" onKeyPress={(event) => { if (onKeyPressValidate(event)) {onCloseSpecModal();} }}/>
        </div>
      </FocusTrap>
    )
  }
}

export default SpecModal;



