import React, { Component } from 'react'

class SalesPage extends Component {

  render() {

    const { onMakePhoneCall, onKeyPressValidate } = this.props

    return (
      <div className="salesMainCont">
        <h1 tabIndex="0" className="salesHeader">SEARCHING FOR A SPECIFIC BOOM OR SCISSOR LIFT FOR SALE? OR LOOKING TO BEAT A SPECIFIC AERIAL LIFT PRICE? GLOBAL SOLUTIONS CAN HELP.</h1>
        <div className="salesBigCont1">
          <div tabIndex="0" className="salesSideTextCont">
            <h1 className="salesHeader">Lift Sales</h1>
            <span className="salesSubHeader">Scissor Lift Sales Across North America</span>
            <p className="salesTxt">Global Solutions has partnered with a network of lift equipment wholesalers across the continent to offer a wider variety of high quality
                                                   scissor and aerial lifts for sale. We supply anything from towable lifts, to rough terrain boom lifts, to electric scissor
                                                   lifts and much more.</p>
            <span className="salesSubHeader">Unbeatable Prices For Aerial Lift Sales</span>
            <p className="salesTxt">Searching for an electric scissor lift for sale? Or perhaps you’re looking to beat a specific scissor lift price?
                                                   Global Solutions has a commitment to providing each and every high-quality boom and scissor lift for sale at affordable prices.
                                                   All of the scissor, boom and aerial lifts for sale at Global Solutions come from a certified fleet that our qualified lift technicians
                                                   diligently inspect and certify, ensuring a profitable investment and long-lasting machine.</p>
           <span className="salesSubHeader">Why Buy A Scissor Lift For Sale At Global Solutions?</span>
           <p className="salesTxt">Whether you buy a scissor lift or boom lift for sale directly from our inventory, or let us help you find one from our equipment
                                                  wholesale partners, you can rest assured that a quality lift will be delivered to you hassle-free. Browse our impressive inventory of
                                                  boom and scissor lifts for sale, and contact our friendly aerial and boom lift sales team to get a free quote today</p>
          </div>
        </div>
        <div className="salesBigCont2">
            <span className="salesHeaderSmall">FINAL LIFT SALES PRICES CAN VARY DEPENDING ON WHAT TYPE OF LIFT EQUIPMENT YOU WANT TO BUY AND HOW QUICKLY YOU NEED IT DELIVERED.</span>
            <div className="salesContactCont">
              <h1 className="salesHeaderLink" onKeyPress={(event) => { if (onKeyPressValidate(event)) {onMakePhoneCall("+13238635034");} }}
                  tabIndex="0" role="link" aria-label="call us +13238635034"
                  onClick={() => onMakePhoneCall("+13238635034")}>CALL NOW: 1-323-863-5034</h1>
              <span className="salesHeader">QUICK QUOTE</span>
              <span tabIndex="0" className="salesTxt">Fill Out The Form Below And We Will Contact You:</span>
              <form className="salesMailForm" action="/GSmailer.php" method="post">
                <input type="hidden" name="initializer" value="Sent_From_Sales_Contact_Form"/>
                <label className="salesMailInputLabel">Full Name: </label>
                <input required className="salesMailInputTxt" type="text" name="full name" aria-label="your full name"></input>
                <label className="salesMailInputLabel">E-Mail: </label>
                <input required className="salesMailInputTxt" type="email" name="email"></input>
                <label className="salesMailInputLabel">Details: </label>
                <textarea rows="10" className="salesMailInputTxt" name="your message" aria-label="details"></textarea>
                <input className="salesFormSubmitBtn" type="submit" name="submit" value="Request Current Sales Info"/>
              </form>
            </div>
        </div>
      </div>
    );
  }
}

export default SalesPage;



